.form {
  width: 100%;
  background-image: url("../../img/airplane.png"), linear-gradient(142deg, rgb(4, 101, 147) 35%, #00b3ff 35%);
  padding-right: 360px;
  padding-top: 170px;
  padding-bottom: 250px;
  background-repeat: no-repeat;
  background-position: 20% 45%, 0 0;
}
@media (max-width: 1800px) {
  .form {
    background-image: linear-gradient(142deg, rgb(4, 101, 147) 35%, #00b3ff 35%);
  }
}
@media (max-width: 1023px) {
  .form {
    padding: 50px 50px 125px 50px;
    background-image: none;
    background-color: #00b3ff;
  }
}

.form__container {
  max-width: 585px;
  margin: 0;
  margin-right: 0;
  margin-left: auto;
}
@media (max-width: 1023px) {
  .form__container {
    margin: 0 auto;
    max-width: 100%;
  }
}

.form__title {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Roboto condensed", "Arial", sans-serif;
  color: #ffffff;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
}
@media (max-width: 1023px) {
  .form__title {
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .form__title {
    font-size: 30px;
    line-height: 42px;
    margin-bottom: 20px;
  }
}

.form__description {
  margin: 0;
  margin-bottom: 35px;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .form__description {
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .form__description {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 40px;
  }
}

.form__wrapper {
  display: grid;
  grid-template-columns: 275px 275px;
  column-gap: 30px;
  row-gap: 20px;
}
@media (max-width: 1023px) {
  .form__wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .form__wrapper {
    display: flex;
    flex-direction: column;
  }
}

.form__input {
  background-color: rgba(4, 20, 39, 0.15);
  border-radius: 3px;
  border: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff;
  padding: 15px;
  width: 100%;
}
.form__input:hover {
  outline: 1px solid #0b5778;
}
.form__input:focus {
  outline: 1px solid #041827;
}
.form__input::placeholder {
  color: #ffffff;
}
.form__input--policy[type=checkbox]:checked + .form__check {
  background-image: url("../../img/svg/check.svg");
}

.form__check {
  display: block;
  position: absolute;
  left: 0;
  top: 65%;
  margin-top: -7px;
  width: 15px;
  height: 14px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.form__label--question {
  grid-column-start: 1;
  grid-column-end: 3;
}
.form__label--question .form__input {
  height: 155px;
}
.form__label--policy {
  display: flex;
  padding-left: 26px;
  position: relative;
}
.form__label--policy::before {
  content: "";
  position: absolute;
  left: 0;
  top: 65%;
  margin-top: -7px;
  width: 15px;
  height: 14px;
  border: solid 1px #ffffff;
  border-radius: 2px;
}

.form__send {
  background-color: #041427;
  border: none;
  border-radius: 3px;
  padding: 15px 25px;
  font-family: "Roboto condensed", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  max-width: 265px;
}
.form__send:hover, .form__send:focus {
  background-color: #0f82b3;
}
.form__send:active {
  background-color: #0b5778;
}

.form__policy-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  text-align: left;
  align-self: end;
}

.advatnages-about-container {
  background-color: #f8fdff;
}

