.services {
  width: 100%;
  background-color: $main-theme-color;
  padding-top: 70px;
  padding-bottom: 140px;


  @media (max-width: $vp-1023) {
    padding-top: 60px;
    padding-bottom: 75px;
  }

  @media (max-width: $vp-767) {
    padding-top: 50px;
    padding-bottom: 86px;
  }
}

.services__title {
  margin: 0;
  font-family: $additional-font;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;
  margin-bottom: 50px;

  &--mobile {
    display: none;
  }

  @media (max-width: $vp-767) {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 40px;

    &--desktop {
      display: none;
    }

    &--mobile {
      display: inline-block;
    }
  }
}

.services__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 85px;
  row-gap: 30px;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.services__container {
  padding-left: 100px;
  padding-right: 100px;
  width: 100%;
  margin: 0 auto;

  @media (max-width: $vp-1023) {
    padding-left: 50px;
    padding-right: 50px;
  }

  @media (max-width: $vp-767) {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.services__service-title {
  width: min-content;
  color: $color-default-white;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  font-family: $additional-font;

  @media (max-width: $vp-767) {
    font-size: 18px;
    line-height: 30px;
  }

  .services__link:hover &,
  .services__link:focus & {
    color: $decoration-color;
  }

  .services__link:active & {
    color: $decoration-color-hover;
  }
}

.services__link {
  display: block;
  width: 350px;
  min-height: 650px;
  padding-top: 50px;
  padding-left: 40px;

  @media (max-width: $vp-1023) {
    min-width: 670px;
    min-height: 250px;
    padding-top: 40px;
    padding-left: 45px;
  }

  @media (max-width: $vp-767) {
    min-width: 270px;
    min-height: 250px;
    padding-top: 30px;
    padding-left: 30px;
  }
}

.services__item {
  background-size: cover;
  position: relative;
  background-image: none;

  &--production {
    background-image: url("../../img/production-desktop.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../../img/production-desktop@2x.jpg");
    }
  }

  &--montage {
    background-image: url("../../img/montage-desktop.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../../img/montage-desktop@2x.jpg");
    }
  }

  &--lights {
    background-image: url("../../img/lights-desktop.jpg");

    @media (min-resolution: $retina-dppx) {
      background-image: url("../../img/lights-desktop@2x.jpg");
    }
  }

  @media (max-width: $vp-1023) {
    &--production {
      background-image: url("../../img/production-tablet.jpg");

      @media (min-resolution: $retina-dppx) {
        background-image: url("../../img/production-tablet@2x.jpg");
      }
    }

    &--montage {
      background-image: url("../../img/montage-tablet.jpg");

      @media (min-resolution: $retina-dppx) {
        background-image: url("../../img/montage-tablet@2x.jpg");
      }
    }

    &--lights {
      background-image: url("../../img/lights-tablet.jpg");

      @media (min-resolution: $retina-dppx) {
        background-image: url("../../img/lights-tablet@2x.jpg");
      }
    }
  }

  @media (max-width: $vp-767) {
    &--production {
      background-image: url("../../img/production-mobile.jpg");

      @media (min-resolution: $retina-dppx) {
        background-image: url("../../img/production-mobile@2x.jpg");
      }
    }

    &--montage {
      background-image: url("../../img/montage-mobile.jpg");

      @media (min-resolution: $retina-dppx) {
        background-image: url("../../img/montage-mobile@2x.jpg");
      }
    }

    &--lights {
      background-image: url("../../img/lights-mobile.jpg");

      @media (min-resolution: $retina-dppx) {
        background-image: url("../../img/lights-mobile@2x.jpg");
      }
    }
  }


  &::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 60px;
    height: 60px;
    background-color: $decoration-color;

    @media (max-width: $vp-767) {
      width: 40px;
      height: 40px;
    }
  }

  ::before {
    content: "";
    position: absolute;
    right: 25px;
    bottom: 25px;
    width: 8px;
    height: 4px;
    border: solid $color-default-white;
    border-width: 0 1px 1px 0;
    padding: 3px;
    transform: rotate(-45deg);
    background-color: transparent;
    z-index: 2;

    @media (max-width: $vp-767) {
      right: 16px;
      bottom: 16px;
      width: 5px;
      height: 2px;
    }
  }
}
